.dashboard {
  position: absolute;
  background: #f4f5f7;
  border-radius: 3px;
  top: 98px;
  bottom: 20px;
  right: 10px;
  left: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 11;
  .dashboard-header {
    margin: 8px;
    margin-left: auto;
    display: flex;
    button.dashboard-close {
      height: 20px;
      width: 20px;
      padding: 0;
      background-color: transparent;
      &:focus-visible {
        outline: 0;
      }
      > span {
        height: 24px;
        width: 24px;
        &::before {
          content: url(../../svg/close.svg);
          max-height: 100%;
          max-width: 100%;
          overflow: hidden;
          pointer-events: none;
        }
      }
    }
  }
  .dashboard-content {
    height: 100%;
    padding: 8px;
    margin: 8px;
    display: grid;
    box-sizing: border-box;
    grid-column-gap: 32px;
    grid-row-gap: 32px;
    overflow-y: auto;
    -webkit-overflow-scrolling: touch;
    -webkit-transform: translate3d(0, 0, 0);
    grid-auto-rows: minmax(500px, 0.5fr);
    grid-template-columns: repeat(2, minmax(0, 1000px));
    @media (max-width: 1400px) {
      grid-template-columns: minmax(0, 1000px);
    }

    &.scroll-visible {
      padding-top: 1px;
    }
    &:last-child {
      padding-bottom: 1px;
    }
    &::-webkit-scrollbar {
      height: 8px;
      width: 8px;
    }

    /* Track */
    &::-webkit-scrollbar-track {
      // margin: 0 5px;
      // border: 5px solid transparent;
      border-radius: 10px;
      // box-shadow: inset 0 0 8px 8px rgba($color: #fff, $alpha: 0.35)d;
      background-color: rgba($color: #fff, $alpha: 0.35);
    }

    /* Handle */
    &::-webkit-scrollbar-thumb {
      // border: 5px solid transparent;
      border-radius: 10px;
      // background-color: #091e4214;
      // box-shadow: inset 0 0 8px 8px rgba($color: #000000, $alpha: 0.15);
      background-color: rgba($color: #000000, $alpha: 0.15);
    }

    > .graph {
      background: #fff;
      width: 100%;
      height: 100%;
      border-radius: 6px;
      box-shadow: 0 3px 5px rgba(9, 30, 66, 0.2), 0 0 1px rgba(9, 30, 66, 0.31);
      > div {
        padding: 16px;
        height: 100%;
        display: flex;
        flex-direction: column;
        > .graph-header {
          margin-bottom: 32px;
          display: flex;
          align-items: center;
          > span:first-of-type {
            display: flex;
            margin-left: 8px;
            margin-right: 8px;
            > h4 {
              margin-right: 8px;
              font-size: 16px;
              line-height: 20px;
              font-weight: 600;
              margin: 0 0 8px;
            }
          }
        }
        > .graph-content {
          position: relative;
          flex-grow: 1;
          // GRAPH COMES HERE
        }
      }
    }
  }
}
